@import url("./fonts/fonts.css");

:root {
    --font-heading: 'MrEaves', sans-serif;
    --font-table-heading: 'ScalySansCaps', sans-serif;
    --font-table: 'ScalySans', sans-serif;
    --font-body: 'BookInsanity', serif;

    --color-bg: hsl(47, 41%, 81%);
    --color-bg-light: hsl(49, 62%, 95%);
    --color-bg-text: hsl(0, 0%, 0%);
    --color-primary: hsl(9, 74%, 20%);
    --color-primary-dark: hsl(31, 100%, 12%);
    --color-primary-text: hsl(0, 0%, 100%);
}

body {
    background-color: var(--color-bg);
}

h1, h2, h3, h4, h5, h6 {
    font-family: var(--font-heading);
    color: var(--color-primary);
    font-weight: bold;
}

a {
    color: var(--color-primary);
}

a:hover {
    text-decoration: none;
}

button {
    background-color: var(--color-primary);
    color: var(--color-primary-text);
    border: none;
    font-family: var(--font-heading);
    text-transform: lowercase;
    font-size: 1em;
    padding: 0.25em 1.5em;
    box-shadow: 0 0.1875em 0.125em -0.0625em hsla(0, 0%, 0%, 0.5);
    cursor: pointer;
}

button:hover {
    background-color: var(--color-primary-dark);
}

button:active {
    background-color: var(--color-primary-dark);
    box-shadow: 0 0 0.125em 0.0625em hsla(0, 0%, 0%, 0.75) inset;
}

/* https://webaim.org/techniques/css/invisiblecontent/ */
.visually-hidden {
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
    height: 1px;
    width: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
}

/* Uninitialized Shell */
.app-footer {
    display: flex;
    justify-content: space-between;
    font-family: var(--font-body);
    padding: 0;
}

.parchment-container {
    position: relative;
    overflow: hidden;
    background-image: url('parchment.png');
    background-size: 100vmax 100vmax;
}

.parchment-effect {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.parchment-effect rect {
    fill: var(--color-bg);
}

.app-title {
    text-align: center;
    margin-bottom: 0.125em;
}

.app-slogan {
    text-align: center;
    font-family: var(--font-body);
}

.app-container {
    max-width: 25rem;
    padding: 1em;
    display: grid;
    grid-template-rows: 1fr auto;
    gap: 1em;
    margin: auto;
}

#app {
    min-height: 100vh;
}
